import React, { useCallback } from 'react';
import { MenuItem } from '@ali/we-design';
import { insertCard } from '@ali/4ever-factory';
import { CalendarCardPluginConfig } from '../types';

const createMenubar = (calendarConfig: CalendarCardPluginConfig) => {
  return function CalendarCardMenu(props: any) {
    const { menuItem, controller } = props;
    const { subjectPlaceholder, timePlaceholder, participantPlaceholder } =
      calendarConfig.locale || {};
    const { title, ...rest } = menuItem;

    const handleClick = useCallback(() => {
      controller.run(
        'onAction',
        insertCard({
          cardType: 'calendar',
          height: 102,
          metadata: {
            isPlaceholder: true,
            subject: subjectPlaceholder,
            time: timePlaceholder,
            topReceivers: [
              {
                isReceiverPlaceholder: true,
                name: participantPlaceholder,
                type: 2,
              },
            ],
          },
        }),
      );
    }, [controller]);

    return (
      <MenuItem {...rest} onClick={handleClick}>
        {title || ''}
      </MenuItem>
    );
  };
};

export default createMenubar;
